/* stylelint-enable */
@font-face {
  font-weight: normal;
  font-family: "Geometria";
  font-style: normal;
  font-display: auto;
  src: url("../fonts/Geometria-Regular.woff2") format("woff2"), url("../fonts/Geometria-Regular.woff") format("woff"); }

@font-face {
  font-weight: 500;
  font-family: "Geometria";
  font-style: normal;
  font-display: auto;
  src: url("../fonts/Geometria-Medium.woff2") format("woff2"), url("../fonts/Geometria-Medium.woff") format("woff"); }

@font-face {
  font-weight: 300;
  font-family: "Geometria";
  font-style: normal;
  font-display: auto;
  src: url("../fonts/Geometria-Light.woff2") format("woff2"), url("../fonts/Geometria-Light.woff") format("woff"); }

html,
body {
  min-width: 375px; }

body {
  padding: 0;
  margin: 0 auto;
  width: auto;
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  background-color: #fbf5f4;
  color: black; }

@media (min-width: 768px) {
  body {
    width: auto; } }

@media (min-width: 1300px) {
  body {
    width: auto; } }

.page-header {
  width: 100%; }

.page-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 375px;
  margin: 0 auto; }

.page-header__logo {
  padding-top: 14px;
  padding-right: 16px; }

.page-header__button {
  display: none; }

.page-header__btn-menu {
  position: absolute;
  top: 16px;
  left: 30px;
  cursor: pointer;
  transition: right .23s;
  z-index: 3;
  width: 20px;
  transform: translateZ(0);
  backface-visibility: hidden; }

.page-header__btn-menu span {
  display: block;
  height: 2px;
  background-color: #f78e6a;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative; }

.page-header__third {
  width: 14px; }

.page-header__btn-menu span::before {
  content: "";
  position: absolute;
  left: -4px;
  width: 2px;
  height: 2px;
  background-color: black; }

.page-header__button:checked ~ .page-header__btn-menu {
  left: 320px;
  top: 25px;
  width: 16px; }

.page-header__button:checked ~ .main-nav {
  top: 0; }

.page-header__button:checked ~ .page-header__btn-menu span.page-header__first {
  width: 20px;
  transform: rotate(45deg); }

.page-header__button:checked ~ .page-header__btn-menu span.page-header__second {
  display: none; }

.page-header__button:checked ~ .page-header__btn-menu span.page-header__third {
  transform: rotate(-45deg);
  top: -9px;
  width: 20px; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main-nav {
  display: block;
  position: fixed;
  list-style: none;
  padding-top: 25px;
  margin: 0;
  box-sizing: border-box;
  box-shadow: 0 5px 12px -6px #444;
  width: 375px;
  background-color: #fbf5f4;
  height: 310px;
  top: -330px;
  transition: all .5s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden; }

.main-nav__list {
  margin: 0;
  margin-top: 10px;
  padding: 0;
  list-style: none; }

.main-nav__item {
  padding-left: 40px;
  margin-bottom: 10px;
  font-size: 14px; }

.main-nav__item a {
  display: inline-block;
  text-decoration: none;
  color: #fc66a2;
  line-height: 1;
  cursor: pointer; }

.main-nav__item a::after {
  display: block;
  content: "";
  height: 2px;
  width: 0;
  background-color: #f78e6a;
  transition: width .3s ease-in-out; }

.main-nav__item a:hover::after,
.main-nav__item a:focus::after {
  width: 100%; }

.main-nav__wrapper {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 40px;
  padding-bottom: 10px;
  border-bottom: 1px solid #4444; }

.main-nav__wrapper-item a {
  text-decoration: none;
  color: #707070;
  font-size: 13px; }

.main-nav__wrapper-item a:hover {
  color: #f78e6a; }

.main-nav__wrapper-item img {
  margin-right: 5px; }

.social {
  display: flex;
  justify-content: space-between;
  width: 110px;
  padding-top: 15px;
  font-size: 10px; }

.social__item a {
  text-decoration: none;
  text-transform: uppercase;
  color: #000000; }

.collection {
  padding-top: 185px;
  padding-left: 185px; }

.collection__header {
  font-size: 45px;
  font-family: Playfair Display;
  margin: 0; }

.welcome {
  width: 375px;
  margin: 0 auto; }

.welcome__image img {
  margin-top: 52px;
  margin-left: 87px; }

.welcome__header {
  position: relative; }

.welcome__title {
  margin: 0;
  margin-top: 40px;
  padding: 40px;
  padding-left: 30px;
  height: 15px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2), linear-gradient(to bottom, #f78e6a, #f78e6a);
  font-size: 130px;
  font-weight: 500;
  letter-spacing: -6.5px;
  color: #f78e6a;
  background-clip: text;
  -webkit-text-fill-color: transparent; }

.welcome__item {
  width: 105px;
  position: absolute;
  top: 30px;
  left: -23px;
  font-size: 16px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2), linear-gradient(to bottom, #f78e6a, #f78e6a);
  transform: rotate(-90deg);
  color: #f78e6a;
  background-clip: text;
  -webkit-text-fill-color: transparent; }

.welcome__description {
  position: relative;
  margin: 0;
  margin-top: 39px;
  margin-left: 87px;
  padding-bottom: 130px;
  font-size: 16px;
  width: 265px;
  line-height: 1.56;
  font-weight: 300;
  color: black; }

.welcome__wrapper {
  position: relative; }

.welcome__wrapper::before {
  content: "";
  position: absolute;
  left: 25px;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2); }

.welcome__description::after {
  content: "";
  position: absolute;
  top: 16px;
  left: -38px;
  width: 2px;
  height: 18px;
  background-color: white; }

.welcome__description::before {
  content: "";
  position: absolute;
  top: 16px;
  left: -38px;
  width: 2px;
  height: 18px;
  transform: rotate(90deg);
  background-color: white; }

.page-footer__container {
  display: flex;
  justify-content: space-between;
  width: 375px;
  margin: 0 auto; }

.page-footer__arrow {
  position: relative;
  margin-top: 8px;
  margin-right: 20px;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2); }

.page-footer__arrow::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 12px;
  width: 0;
  height: 0;
  border: 6px solid;
  border-color: white;
  border-bottom: 0;
  border-right: 3px solid transparent;
  border-left: 3px solid transparent; }

.page {
  margin-left: 20px;
  padding-bottom: 20px; }

.page__current {
  padding-top: 1px;
  font-size: 30px;
  font-weight: 300;
  line-height: 0.8;
  color: #f88d6c;
  background-clip: text;
  background-image: linear-gradient(to right, #f78e6a, #fc66a2), linear-gradient(to bottom, #f88d6c, #f88d6c);
  -webkit-text-fill-color: transparent; }

.page__all {
  font-size: 14px;
  font-weight: 300;
  line-height: 1.71;
  color: #f88d6c;
  background-clip: text;
  background-image: linear-gradient(to right, #f78e6a, #fc66a2), linear-gradient(to bottom, #f88d6c, #f88d6c);
  -webkit-text-fill-color: transparent; }
