.welcome {
  width: 375px;
  margin: 0 auto;
}

.welcome__image img {
  margin-top: 52px;
  margin-left: 87px;
}

.welcome__header {
  position: relative;
}

.welcome__title {
  margin: 0;
  margin-top: 40px;
  padding: 40px;
  padding-left: 30px;
  height: 15px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2), linear-gradient(to bottom, #f78e6a, #f78e6a);
  font-size: 130px;
  font-weight: 500;
  letter-spacing: -6.5px;
  color: #f78e6a;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.welcome__item {
  width: 105px;
  position: absolute;
  top: 30px;
  left: -23px;
  font-size: 16px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2), linear-gradient(to bottom, #f78e6a, #f78e6a);
  transform: rotate(-90deg);
  color: #f78e6a;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.welcome__description {
  position: relative;
  margin: 0;
  margin-top: 39px;
  margin-left: 87px;
  padding-bottom: 130px;
  font-size: 16px;
  width: 265px;
  line-height: 1.56;
  font-weight: 300;
  color: black;
}

.welcome__wrapper {
  position: relative;
}

.welcome__wrapper::before {
  content: "";
  position: absolute;
  left: 25px;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.welcome__description::after {
  content: "";
  position: absolute;
  top: 16px;
  left: -38px;
  width: 2px;
  height: 18px;
  background-color: white;
}

.welcome__description::before {
  content: "";
  position: absolute;
  top: 16px;
  left: -38px;
  width: 2px;
  height: 18px;
  transform: rotate(90deg);
  background-color: white;
}
