.page-header {
  width: 100%;
}

.page-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 375px;
  margin: 0 auto;
}

.page-header__logo {
  padding-top: 14px;
  padding-right: 16px;
}

.page-header__button {
  display: none;
}

.page-header__btn-menu {
  position: absolute;
  top: 16px;
  left: 30px;
  cursor: pointer;
  transition: right .23s;
  z-index: 3;
  width: 20px;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.page-header__btn-menu span {
  display: block;
  height: 2px;
  background-color: #f78e6a;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative;
}

.page-header__third {
  width: 14px;
}

.page-header__btn-menu span::before {
  content: "";
  position: absolute;
  left: -4px;
  width: 2px;
  height: 2px;
  background-color: black;
}

.page-header__button:checked ~ .page-header__btn-menu {
  left: 320px;
  top: 25px;
  width: 16px;
}

.page-header__button:checked ~ .main-nav {
  top: 0;
}

.page-header__button:checked ~ .page-header__btn-menu span.page-header__first {
  width: 20px;
  transform: rotate(45deg);
}

.page-header__button:checked ~ .page-header__btn-menu span.page-header__second {
  display: none;
}

.page-header__button:checked ~ .page-header__btn-menu span.page-header__third {
  transform: rotate(-45deg);
  top: -9px;
  width: 20px;
}
