.page {
  margin-left: 20px;
  padding-bottom: 20px;
}

.page__current {
  padding-top: 1px;
  font-size: 30px;
  font-weight: 300;
  line-height: 0.8;
  color: #f88d6c;
  background-clip: text;
  background-image: linear-gradient(to right, #f78e6a, #fc66a2), linear-gradient(to bottom, #f88d6c, #f88d6c);
  -webkit-text-fill-color: transparent;
}

.page__all {
    font-size: 14px;
    font-weight: 300;
    line-height: 1.71;
    color: #f88d6c;
    background-clip: text;
    background-image: linear-gradient(to right, #f78e6a, #fc66a2), linear-gradient(to bottom, #f88d6c, #f88d6c);
    -webkit-text-fill-color: transparent;
}
