.page-footer__container {
  display: flex;
  justify-content: space-between;
  width: 375px;
  margin: 0 auto;
}

.page-footer__arrow {
  position: relative;
  margin-top: 8px;
  margin-right: 20px;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.page-footer__arrow::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 12px;
  width: 0;
  height: 0;
  border: 6px solid;
  border-color: white;
  border-bottom: 0;
  border-right: 3px solid transparent;
  border-left: 3px solid transparent;
}
